.header {
  background: #333;
  color: #fff;
  padding: 1rem;
  margin-bottom: 15px;
}

h2 small {
  font-size: 65%;
  font-weight: 400;
  line-height: 1;
  color: #777;
}

@media only screen and (max-width: 500px) {

  table,
  thead,
  tbody,
  th,
  td,
  tr {
    display: block;
  }
}